import * as React from 'react';
import './portal.css';
import { ActiveTabProvider, FieldProvider, ModalHistoryProvider, OptionsProvider, ValidationProvider, DataHistoryProvider } from './context/provider';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Router from './router';

function App() {
  return (
    <FieldProvider>
      <ValidationProvider>
        <OptionsProvider>
          <ActiveTabProvider>
            <DataHistoryProvider>
              <ModalHistoryProvider>
                <Router />
                <ToastContainer style={{ zIndex: 10000 }} />
              </ModalHistoryProvider>
            </DataHistoryProvider>
          </ActiveTabProvider>
        </OptionsProvider>
      </ValidationProvider>
    </FieldProvider>
  );
}

export default App;
